import React from 'react';
import Slider from 'views/Slider/v1/Slider.js';
import Text from 'views/Text/Text/v1/Text.js';

const dummyObj = {target: {}};
const Scale = (props) => {
    const handleChange = (id, val) => {
        dummyObj.target.name = id;
        dummyObj.target.value = val;
        props.handleChange(dummyObj);
    }
    return <div  className="form-element">
        <Text className="text-center">{props.text}</Text>
        <Slider id={props.id} key={props.id} 
            min={props.item && props.item.min || 1} max={props.item && props.item.max || 5} step={props.item.step && props.item.step || 1} 
            value={props.value?props.value:3} onInput={handleChange} />
    </div>
}

export default Scale;