import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import Text from 'views/Text/Text/v1/Text.js';
import Button from 'views/Button/v1/Button.js';
import './styles.css';
import {log} from 'utilities/Measure';

const MultipleChoice = (props) => {
    log(`Form MultipleChoice props`, props);
    return <div key={props.id} className="container form-element width-full">
        <Text className="text-center">{props.text}</Text>
        {props.options.map((e, i) => {
            return <div className="radio" key={i}><label key={props.id}>
            <input name={props.id} key={e} type="checkbox" value={e} checked={props.value === e} onChange={props.handleChange} />
            <span className="checkmark"></span>
            <span className="radio-item">{e}</span>
          </label></div>
        })}
    </div>
}

export default MultipleChoice;