import React from 'react';
import styled from 'styled-components';
import Text from 'views/Text/Text/v1/Text.js';

const Message = (props) => {

    return <div className="form-element">
        <Text className="text-center">{props.text}</Text>
        <Box className="bg-grad-pp"  key={props.id} name={props.id} id={props.id}
            onChange={props.handleChange} value={props.value} placeholder={props.placeholder}  
        />
    </div>
}
//className="bg-grad-pp" 
export default Message;

const Box = styled.textarea`
    width: 100%;
    color: ${props => props.theme.text};
    background-color: ${props => props.theme.neutral};
    border: none;
    outline: 0;
    padding: 10px;
`