import React from 'react';
import IconButton from 'views/IconButton/v1/IconButton.js';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import {log} from 'utilities/Measure.js';
import Text from 'views/Text/Text/v1/Text';

const Profiles = (props) => {
    const renderTwitter = () => {
        return <a href={`https://twitter.com/stimsims`} target="_blank">
            <IconButton icon="icon-twitter" color="#00aced" hover={"white"} /></a>
    }
    return (
        <div className="text-center">
            {renderTwitter()}
        </div>
    )
}

export default Profiles;


