import React, {useState} from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Text from 'views/Text/Text/v1/Text.js';
import Button from 'views/Button/v1/Button.js';
import MC from './views/MultipleChoice';
import Email from './views/Email';
import TextArea from './views/TextArea';
import Scale from './views/Scale';
import {Event} from 'utilities/Create';
import {sendError, log, sendEvent, sendPageview, initializeAnalytic, trackPersistent, 
      clearPersistantValues, TRACKER_WEB, TRACKER_POSTS} from 'utilities/Measure.js';
import Subscribable from 'views/hoc/Subscribable';

const Form = (props) => {
    const [error, setError] = useState(null);
    const [answers, setAnswers] = useState(props.form.reduce((agg, e, i) => {
      if(e.value){
        agg[e.id] = e.value;
      }
      return agg;
    }, {}));
    const handleChange = e => {
        setAnswers({...answers, [e.target.name]: e.target.value});
    }
    const encode = (object) => {
        var encodedString = '';
        for (var prop in object) {
            if (object.hasOwnProperty(prop)) {
                if (encodedString.length > 0) {
                    encodedString += '&';
                }
                encodedString += encodeURI(prop + '=' + object[prop]);
            }
        }
        return encodedString;
    }
  
    const handleSubmit = (id, e) => {

        let valid = true;
        props.form.map(e => {
          if(!answers.hasOwnProperty(e.id)){
            setError('Please complete the form before submitting');
            valid = false;
          }
        });
        if(valid){
            let value;
            if(props.analytics){
              for(let answer in answers){
                let item = props.form.filter(e => e.id === answer)[0];
                if(item.dimension){
                  trackPersistent(item.dimension, answers[answer]);
                }else{
                  value = answers[answer] || item.value;
                  if(value){
                    if(isNaN(answers[answer])){
                      sendEvent(new Event(item.category || 'report', answer, answers[answer] + "", null));
                    }else{
                      sendEvent(new Event(item.category || 'report', answer, null, answers[answer]));
                    }
                  }
                }
              }
              if(props.onFormOver){
                  props.onFormOver();
              }
          }
          if(props.netlify){
            props.doSubscribe('form', ()=>{
                  return fetch(`/`, {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    body: encode({
                      'form-name': props.id,//form.getAttribute('name'),
                      ...answers,
                    }),
                  }
                )},
                () =>{
                    if(props.onFormOver){
                        props.onFormOver();
                    }
                },
                error => {
                  sendError(`feedback form error: ${error}`, false);
              }
            )
          }
        }

  

    }
    const renderFormElement = (e, i) => {
        switch(e.type){
            case 'email': return <Email id={e.id} key={e.id} value={answers[e.id]} handleChange={handleChange} />
            case 'mc': return <MC id={e.id} key={e.id} options={e.options} text={e.text}
                    value={answers[e.id]} handleChange={handleChange} />
            case 'text': return <TextArea id={e.id} key={e.id} placeholder={e.placeholder}
                text={e.text} value={answers[e.id]} handleChange={handleChange}/>
            case 'scale': return <Scale id={e.id} key={e.id} text={e.text} item={e}
                value={answers[e.id]} handleChange={handleChange} />
            default: return <Text>Unknown for element {e.type}</Text>
        }
    }
    const wrapFormContent = (content) => {
      if(props.netlify){
        return  <form
                  //netlify
                  name={props.id}
                  method="post"
                  data-netlify="true"
                  data-netlify-honeypot="bot-field"
                  onSubmit={handleSubmit}
                  className="text-center"
                  data-netlify-recaptcha="true"
                >
                  {/* The `form-name` hidden field is required to support form submissions without JavaScript */}
                  <input type="hidden" name="form-name" value={props.id} />
                  <div hidden>
                    <label>
                      Donâ€™t fill this out:{' '}
                      <input name="bot-field" onChange={handleChange} />
                    </label>
                  </div>
                  {content}
                  <div data-netlify-recaptcha="true"></div>
                  <Button glow onInput={handleSubmit}>
                      Send
                    </Button>
                </form>
      }else{
        return  <form
                    name={props.id}
                    method="post"
                    onSubmit={handleSubmit}
                    className="text-center"
                  >
                    {content}
                    {error && <Text className="text-center" color="error">{error}</Text>}
                    <Button glow onInput={handleSubmit}>
                        {props.btnText || 'send'}
                      </Button>
                  </form>
      }
    }
    return (
      <FormContainer id={props.id} key={props.key}>
        <section className="content">

              {props.title && <Text tag="h1" color="primary" className="text-xlarge text-center">{props.title}</Text>}
              {props.subtitle && <Text tag="h2" className="text-center" color={props.title?'text':'primary'}>{props.subtitle}</Text>}
              {wrapFormContent(props.form.map((e, i) => {
                      return renderFormElement(e, i)
              }))}
        </section>
      </FormContainer>
    )
  }

Form.propTypes = {
    title: PropTypes.string,
    subtitle: PropTypes.string,
    form: PropTypes.array.isRequired,
    id: PropTypes.string.isRequired,
    netlify: PropTypes.bool,
    analytics: PropTypes.bool,
    onFormOver: PropTypes.func,
    
}

export default Subscribable(Form);

const FormContainer = styled.div`
    ${props => props.hidden?`
        position: absolute;
        opacity:0;
    `:``}
    height: 100%;
    width:100%;
    max-width: ${props=>props.theme.mediaL};
    margin: auto;
    .form-element{
        margin: ${props => props.theme.spaceM}% auto;
    }
    ::-webkit-scrollbar {
        width: 12px;
        margin-top: 57px;
    }
    ::-webkit-scrollbar-track {
        background-color: #271D29;
    }
    
    ::-webkit-scrollbar-thumb {
        background-color: #523957;
        border-radius: 10px;
    }
`

