import React from 'react';
import styled from 'styled-components';
import Text from 'views/Text/Text/v1/Text.js';

const EmailView = (props) => {

    return <Email className="form-element">
        <Text className="text-center">e-mail</Text>
        <input className="email bg-grad-pp" type="email" name="email" 
            onChange={props.handleChange} value={props.value || ''} 
        />
    </Email>
}

export default EmailView;

const Email = styled.div`
    .email{
        width:100%;
        outline: 0;
        border-left: none;
        border-right: none;
        color: ${props => props.theme.text};
        text-align: center;
        select: {
            appearance: none; 
        }
    }
    input:-webkit-autofill{
        -webkit-text-fill-color:${props => props.theme.text};
        -webkit-box-shadow: 0 0 0px 1000px #000 inset;
        width:100%;
    }
`